<template>
  <div class="container-wrapper">
    <!-- 头部标题 -->
    <div class="header">
      <h1>八大商圈地图服务</h1>
    </div>

    <!-- 地图与天气预报容器 -->
    <div id="map-container">
      <!-- 高德地图容器 -->
      <div id="container"></div>
      <!-- 天气预报面板 -->
      <div class="info" id="weatherPanel" v-show="isWeatherVisible">
        <h4>预报天气</h4><hr>
        <p id="forecast"></p>
      </div>
    </div>

    <!-- 左侧功能控件面板 -->
    <div class="input-card">
      <!-- 基础控件开关 -->
      <div class="input-item">
        <input type="checkbox" checked @change="toggleScale($event)" /> 比例尺
      </div>
      <div class="input-item">
        <input type="checkbox" checked id="toolbar" @change="toggleToolBar($event)" /> 工具条
      </div>
      <div class="input-item">
        <input type="checkbox" checked id="controlBar" @change="toggleControlBar($event)" /> 工具条方向盘
      </div>
      <div class="input-item">
        <input type="checkbox" checked id="overview" @change="toggleOverViewShow($event)" /> 显示鹰眼
      </div>

      <!-- 实时路况按钮 -->
      <div class="input-item traffic-control">
        <button class="btn" @click="toggleTraffic">
          {{ isTrafficVisible ? '隐藏实时路况' : '显示实时路况' }}
        </button>
      </div>

      <!-- 天气预报按钮 -->
      <div class="input-item weather-control">
        <button class="btn" @click="toggleWeather">
          {{ isWeatherVisible ? '隐藏天气预报' : '显示天气预报' }}
        </button>
      </div>

      <!-- 商圈下拉菜单 -->
      <div class="input-item business-district-control">
        <div class="dropdown" id="businessDistrictDropdown" @click.stop="toggleDropdown">
          <button class="dropdown-btn">
            选择商圈 <i class="fas fa-chevron-down" :style="{ transform: dropdownActive ? 'rotate(180deg)' : 'none' }"></i>
          </button>
          <div class="dropdown-content" v-show="dropdownActive">
            <button @click="goToBusinessDistrict('新街口商圈:<br>德基广场、中央商场、新百等', [118.784136, 32.041806])">新街口商圈</button>
            <button @click="goToBusinessDistrict('夫子庙商圈:<br>水游城、水平方、万象天地等', [118.781871, 32.021797])">夫子庙商圈</button>
            <button @click="goToBusinessDistrict('元通商圈:<br>IFC、华采天地、龙湖河西天街等', [118.7211449, 31.995371])">元通商圈</button>
            <button @click="goToBusinessDistrict('江东商圈:<br>金鹰世界、建邺万达广场等', [118.73968, 32.028573])">江东商圈</button>
            <button @click="goToBusinessDistrict('江北商圈:<br>江北虹悦城、宝龙时代广场等', [118.642525, 32.06958])">江北商圈</button>
            <button @click="goToBusinessDistrict('桥北商圈:<br>南京弘阳广场、桥北万象汇等', [118.726639, 32.134721])">桥北商圈</button>
            <button @click="goToBusinessDistrict('百家湖商圈:<br>景枫中心、金鹰天地广场等', [118.820845, 31.931942])">百家湖商圈</button>
            <button @click="goToBusinessDistrict('仙林商圈:<br>金鹰湖滨天地、九霄梦天地等', [118.930412, 32.098663])">仙林商圈</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BusinessDistrictMap',
  data() {
    return {
      // 地图核心实例
      map: null,
      // 控件实例
      scale: null,
      toolBar: null,
      controlBar: null,
      overView: null,
      trafficLayer: null,
      // 状态变量
      isTrafficVisible: true, // 实时路况显示状态
      isWeatherVisible: true, // 天气预报显示状态
      dropdownActive: false, // 下拉菜单展开状态
      // 商圈/天气标记与信息窗口
      businessMarker: null,
      businessInfoWindow: null,
      weatherMarker: null,
      weatherInfoWin: null
    };
  },
  mounted() {
    // 1. 初始化高德地图安全配置
    window._AMapSecurityConfig = {
      securityJsCode: "4d8d783a5463853e63b0af39aa503379"
    };

    // 2. 加载高德地图依赖（若未在public/index.html引入）
    this.loadAMapScripts();

    // 3. 监听全局点击，关闭下拉菜单
    document.addEventListener('click', this.closeDropdownOnOutsideClick);

    // 4. 监听窗口 resize，适配地图尺寸
    window.addEventListener('resize', this.handleWindowResize);
  },
  beforeUnmount() {
    // 清理事件监听，避免内存泄漏
    document.removeEventListener('click', this.closeDropdownOnOutsideClick);
    window.removeEventListener('resize', this.handleWindowResize);

    // 销毁地图实例（可选，优化性能）
    if (this.map) {
      this.map.destroy();
    }
  },
  methods: {
    // ---------------------- 地图初始化核心方法 ----------------------
    initMap() {
      // 初始化地图
      this.map = new AMap.Map('container', {
        center: [118.79, 32.06],
        zoom: 12,
        viewMode: '3D',
        resizeEnable: true
      });

      // 初始化基础控件
      this.initMapControls();

      // 初始化实时路况图层
      this.initTrafficLayer();

      // 初始化天气预报
      this.initWeather();
    },

    // 初始化地图控件（比例尺、工具条、鹰眼等）
    initMapControls() {
      // 比例尺
      this.scale = new AMap.Scale();
      // 工具条
      this.toolBar = new AMap.ToolBar({
        position: { top: '110px', right: '40px' }
      });
      // 工具条方向盘
      this.controlBar = new AMap.ControlBar({
        position: { top: '10px', right: '10px' }
      });
      // 鹰眼
      this.overView = new AMap.HawkEye({ opened: false });

      // 添加控件到地图
      this.map.addControl(this.scale);
      this.map.addControl(this.toolBar);
      this.map.addControl(this.controlBar);
      this.map.addControl(this.overView);
    },

    // 初始化实时路况图层
    initTrafficLayer() {
      this.trafficLayer = new AMap.TileLayer.Traffic({
        zIndex: 10,
        zooms: [7, 22]
      });
      this.trafficLayer.setMap(this.map);
    },

    // ---------------------- 下拉菜单控制 ----------------------
    toggleDropdown() {
      this.dropdownActive = !this.dropdownActive;
    },

    // 点击外部关闭下拉菜单
    closeDropdownOnOutsideClick(event) {
      const dropdown = document.getElementById('businessDistrictDropdown');
      if (dropdown && !dropdown.contains(event.target)) {
        this.dropdownActive = false;
      }
    },

    // ---------------------- 商圈定位功能 ----------------------
    goToBusinessDistrict(name, coords) {
      // 移动地图到目标商圈
      this.map.setCenter(coords);
      this.map.setZoom(16);

      // 移除已有商圈标记和信息窗口
      if (this.businessMarker) this.map.remove(this.businessMarker);
      if (this.businessInfoWindow) this.businessInfoWindow.close();

      // 自定义标记图标（需替换为项目实际图片路径）
      const customIcon = new AMap.Icon({
        size: new AMap.Size(32, 32),
        image: require('@/assets/logo.png'), // Vue中建议用require引入图片
        imageSize: new AMap.Size(32, 32)
      });

      // 创建新标记
      this.businessMarker = new AMap.Marker({
        map: this.map,
        position: coords,
        icon: customIcon,
        zIndex: 20
      });

      // 创建商圈信息窗口
      this.businessInfoWindow = new AMap.InfoWindow({
        content: `<div class="business-label">${name}</div>`,
        isCustom: true,
        offset: new AMap.Pixel(0, -40),
        autoMove: false
      });

      // 打开信息窗口
      this.businessInfoWindow.open(this.map, this.businessMarker.getPosition());

      // 标记点击事件
      this.businessMarker.on('click', () => {
        this.businessInfoWindow.open(this.map, this.businessMarker.getPosition());
      });

      // 关闭下拉菜单
      this.dropdownActive = false;
    },

    // ---------------------- 实时路况切换 ----------------------
    toggleTraffic() {
      if (this.isTrafficVisible) {
        this.trafficLayer.hide();
      } else {
        this.trafficLayer.show();
      }
      this.isTrafficVisible = !this.isTrafficVisible;
    },

    // ---------------------- 基础控件切换 ----------------------
    toggleScale(event) {
      event.target.checked ? this.scale.show() : this.scale.hide();
    },

    toggleToolBar(event) {
      event.target.checked ? this.showToolBar() : this.hideToolBar();
    },

    toggleControlBar(event) {
      event.target.checked ? this.controlBar.show() : this.controlBar.hide();
    },

    toggleOverViewShow(event) {
      event.target.checked ? this.overView.show() : this.overView.hide();
    },

    showToolBar() {
      document.getElementById('toolbar').checked = true;
      this.toolBar.show();
    },

    hideToolBar() {
      document.getElementById('toolbar').checked = false;
      this.toolBar.hide();
    },

    // ---------------------- 天气预报功能 ----------------------
    initWeather() {
      AMap.plugin('AMap.Weather', () => {
        const weather = new AMap.Weather();

        // 1. 查询实时天气（地图标记信息窗口）
        weather.getLive('南京市', (err, data) => {
          if (err) return;

          const liveStr = [
            '<h4>实时天气</h4><hr>',
            `<p>城市/区：${data.city}</p>`,
            `<p>天气：${data.weather}</p>`,
            `<p>温度：${data.temperature}℃</p>`,
            `<p>风向：${data.windDirection}</p>`,
            `<p>风力：${data.windPower} 级</p>`,
            `<p>空气湿度：${data.humidity}</p>`,
            `<p>发布时间：${data.reportTime}</p>`
          ];

          // 自定义天气标记图标（需替换为项目实际图片路径）
          const customIcon = new AMap.Icon({
            size: new AMap.Size(32, 32),
            image: require('@/assets/logo.png'),
            imageSize: new AMap.Size(32, 32)
          });

          // 创建天气标记
          this.weatherMarker = new AMap.Marker({
            map: this.map,
            position: this.map.getCenter(),
            icon: customIcon,
            zIndex: 15
          });

          // 创建天气信息窗口
          this.weatherInfoWin = new AMap.InfoWindow({
            content: `<div class="info" style="position:inherit;margin-bottom:0;">${liveStr.join('')}</div><div class="sharp"></div>`,
            isCustom: true,
            offset: new AMap.Pixel(15, -3)
          });

          // 打开信息窗口
          this.weatherInfoWin.open(this.map, this.weatherMarker.getPosition());

          // 鼠标悬停重新打开
          this.weatherMarker.on('mouseover', () => {
            this.weatherInfoWin.open(this.map, this.weatherMarker.getPosition());
          });
        });

        // 2. 查询未来4天预报（右侧面板）
        weather.getForecast('南京市', (err, data) => {
          if (err) return;

          const forecastStr = data.forecasts.map(day => 
            `${day.date} <span class="weather">${day.dayWeather}</span> ${day.nightTemp}~${day.dayTemp}℃`
          );

          document.getElementById('forecast').innerHTML = forecastStr.join('<br><br>');
        });
      });
    },

    // 切换天气预报显示/隐藏
    toggleWeather() {
      if (this.isWeatherVisible) {
        // 隐藏：面板、标记、信息窗口
        this.weatherMarker.hide();
        this.weatherInfoWin.close();
      } else {
        // 显示：面板、标记、信息窗口
        this.weatherMarker.show();
        this.weatherInfoWin.open(this.map, this.weatherMarker.getPosition());
      }
      this.isWeatherVisible = !this.isWeatherVisible;
    },

    // ---------------------- 窗口 resize 适配 ----------------------
    handleWindowResize() {
      if (this.map) this.map.resize();

      // 重新定位信息窗口
      if (this.weatherInfoWin && this.weatherMarker && this.isWeatherVisible) {
        this.weatherInfoWin.open(this.map, this.weatherMarker.getPosition());
      }
      if (this.businessInfoWindow && this.businessMarker) {
        this.businessInfoWindow.open(this.map, this.businessMarker.getPosition());
      }
    },

    // ---------------------- 动态加载高德地图脚本 ----------------------
    loadAMapScripts() {
      // 1. 加载es5兼容库
      const es5Script = document.createElement('script');
      es5Script.src = 'https://cache.amap.com/lbs/static/es5.min.js';
      es5Script.onload = () => {
        // 2. 加载高德地图JSAPI
        const mapScript = document.createElement('script');
        mapScript.src = '//webapi.amap.com/maps?v=2.0&key=c43b5af1cea9dce07e948a07b5b91b9b&&plugin=AMap.Scale,AMap.HawkEye,AMap.ToolBar,AMap.ControlBar,AMap.Weather';
        mapScript.onerror = (error) => {
          console.error('高德地图加载失败:', error);
        };
        mapScript.onload = () => {
          // 脚本加载完成后初始化地图
          this.initMap();
        };
        document.body.appendChild(mapScript);
      };
      document.body.appendChild(es5Script);
    }
  }
};
</script>

